.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 15vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

button, input[type=submit] {
  background-color: transparent;
  cursor: pointer;
  text-transform: uppercase;
}

.submit-button {
  margin: 25px 10px 10px 10px;
  font-family: 'Univers', sans-serif;
  font-weight: 600;
  font-size: 20px;
  color: #fff;
  transition-property: all;
  transition-timing-function: cubic-bezier(.4,0,.2,1);
  transition-duration: .15s;
  border: 3px solid;
  border-color: #e5e5e5;
  border-radius: 5px;
  padding: 7px 15px;
}

.submit-button:hover {
  color: #fbb10a;
  text-shadow: #fbb10a 0 0 10px;
  border-color: #fbb10a;
}

.submit-button:active {

}

input[type=range] {
  width: 150px;
}

select {
  display:table-row;
  overflow: hidden;
}
option {
  display:table-cell;
}



ul {
  list-style-type: none;
}

.server {
  min-width: 90vw;
  border: 3px solid #fff3;
  /* border-radius: 10px; */
  margin-bottom: 10px;
  text-align: start;
  display: flex;
  font-family: 'Univers', sans-serif;
  align-items: center;
}

.server-map-image {
  max-width: 150px;
}

.server-flag-image {
  padding: 10px 0 0 0;
  max-width: 30px;
}

.uppercase {
  text-transform: uppercase;
}